exports.push([module.id, "\n.region[data-v-5d968408] {\r\n  max-height: 400px;\r\n  overflow: auto;\n}\n.province[data-v-5d968408] {\r\n  padding: 20px 0;\n}\n.city[data-v-5d968408] {\r\n  padding: 5px 10px;\r\n  border-top: 1px solid #cccccc;\n}\n.area[data-v-5d968408] {\r\n  padding: 1px 15px;\n}\n.export[data-v-5d968408] {\r\n  float: right;\r\n\r\n  margin-right: 70px;\n}\r\n", "", {"version":3,"sources":["F:/仲达集团/2020-3-18总后台管理系统/src/pages/filterIncome/src/pages/filterIncome/index.vue"],"names":[],"mappings":";AAkTA;EACA,kBAAA;EACA,eAAA;CACA;AACA;EACA,gBAAA;CACA;AACA;EACA,kBAAA;EACA,8BAAA;CACA;AACA;EACA,kBAAA;CACA;AACA;EACA,aAAA;;EAEA,mBAAA;CACA","file":"index.vue","sourcesContent":["<template>\r\n  <div>\r\n    <ctbHead>\r\n      <router-link :class=\"seCurId==item.id? 'cur':''\"\r\n                   v-for=\"item in authList\"\r\n                   :key=\"item.id\"\r\n                   :to=\"{path:item.action,query:{id:curId}}\"\r\n                   :v-key=\"item.id\">\r\n        {{item.name}}\r\n      </router-link>\r\n    </ctbHead>\r\n    <div class=\"container\">\r\n      <div class=\"quote\">\r\n        <div class=\"quote-ele\">\r\n          <i></i>\r\n          配送中心-滤芯收入\r\n          <div class='export'>\r\n            <el-button type=\"primary\"\r\n                       @click=\"EXPort()\">导出</el-button>\r\n          </div>\r\n        </div>\r\n      </div>\r\n      <!-- 省市搜索 -->\r\n      <div style=\"margin-left:30px\">\r\n\r\n        <!-- <el-input v-model=\"company\"\r\n                  placeholder=\"请输入公司名称\"\r\n                  style=\"width:15%\"\r\n                  clearable /> -->\r\n        <el-select placeholder=\"请选择省\"\r\n                   v-model=\"provinceId\"\r\n                   @change=\"getCity\"\r\n                   clearable>\r\n          <el-option v-for=\"(item,index) in provinceList\"\r\n                     :key=\"index\"\r\n                     :label=\"item.name\"\r\n                     :value=\"item.id\"></el-option>\r\n        </el-select>\r\n        <el-select placeholder=\"请选择市\"\r\n                   v-model=\"cityId\"\r\n                   @change=\"init()\">\r\n          <el-option v-for=\"(item,index) in cityList\"\r\n                     :key=\"index\"\r\n                     :label=\"item.name\"\r\n                     :value=\"item.id\"></el-option>\r\n        </el-select>\r\n        <el-date-picker type=\"month\"\r\n                        placeholder=\"选择月份\"\r\n                        v-model=\"month\"\r\n                        @change=\"search\"\r\n                        value-format=\"yyyy-MM-dd\">\r\n        </el-date-picker>\r\n      </div>\r\n      <!-- 列表 -->\r\n      <el-table :data=\"list\">\r\n        <el-table-column align=\"center\"\r\n                         label=\"运营中心名称\">\r\n          <template slot-scope=\"scope\">\r\n            {{`${scope.row.province}${scope.row.city}`}}<span v-if=\"scope.row.province\">运营中心</span>\r\n          </template>\r\n        </el-table-column>\r\n        <el-table-column align=\"center\"\r\n                         label=\"滤芯补贴收入\"\r\n                         prop=\"total_money\">\r\n          <template slot-scope=\"scope\">\r\n            <el-button type=\"text\"\r\n                       @click=\"incomeDetail(scope.row)\">{{scope.row.total_money}}</el-button>\r\n          </template>\r\n        </el-table-column>\r\n        <el-table-column align=\"center\"\r\n                         label=\"月份\"\r\n                         prop=\"month\">\r\n          <template slot-scope=\"scope\">\r\n            <div v-if=\"scope.row.month\">\r\n              {{scope.row.month}}月\r\n            </div>\r\n          </template>\r\n        </el-table-column>\r\n      </el-table>\r\n\r\n      <!-- 地区详情 -->\r\n      <el-dialog title=\"地区详情\"\r\n                 :visible.sync=\"regionDetailsVisible\"\r\n                 center>\r\n        <div v-if=\"regionList && regionList.length>0\"\r\n             class=\"region\">\r\n          <div v-for=\"(item,index) in regionList\"\r\n               class=\"province\"\r\n               :key=\"index\">\r\n            <h3 style=\"margin:5px 0\">{{item.name}}</h3>\r\n            <div v-if=\"item.son && item.son.length>0\">\r\n              <div v-for=\"(city,cityIndex) in item.son\"\r\n                   :key=\"cityIndex\"\r\n                   class=\"city\">\r\n                <h4>{{city.name}}</h4>\r\n                <div v-if=\"city.son && city.son.length>0\">\r\n                  <div v-for=\"(area,areaIndex) in city.son\"\r\n                       :key=\"areaIndex\"\r\n                       class=\"area\">\r\n                    {{area.name}}\r\n                  </div>\r\n                </div>\r\n              </div>\r\n            </div>\r\n          </div>\r\n        </div>\r\n      </el-dialog>\r\n      <!-- 收入明细 -->\r\n      <el-dialog title=\"收入明细\"\r\n                 center\r\n                 :visible.sync=\"incomeVisible\">\r\n        <el-table :data=\"incomeList\">\r\n          <el-table-column align=\"center\"\r\n                           label=\"维修厂名称\"\r\n                           prop=\"company\">\r\n\r\n          </el-table-column>\r\n          <el-table-column align=\"center\"\r\n                           label=\"邦保养卡号\"\r\n                           prop=\"card_number\"></el-table-column>\r\n          <el-table-column align=\"center\"\r\n                           label=\"车牌号\"\r\n                           prop=\"plate\"></el-table-column>\r\n          <el-table-column align=\"center\"\r\n                           label=\"收入金额( 元 )\"\r\n                           prop=\"money\"></el-table-column>\r\n          <el-table-column align=\"center\"\r\n                           label=\"收入时间\"\r\n                           prop=\"create_time\"></el-table-column>\r\n        </el-table>\r\n        <!-- 收入明细分页 -->\r\n        <div class=\"page_center\">\r\n          <paging :page-count=\"incomeCount\"\r\n                  :page=\"incomePage\"\r\n                  @index=\"(e)=>{\r\n                    incomePage=e,\r\n                    incomeDetail(currentIncomeData)\r\n                }\"></paging>\r\n        </div>\r\n      </el-dialog>\r\n      <!-- 分页 -->\r\n      <div :class=\"list && list.length<=7?'page-nav':'page_center'\"\r\n           v-show=\"list && list.length>0\">\r\n        <paging :page-count=\"pageCount\"\r\n                :page=\"page\"\r\n                @index=\"paging\"></paging>\r\n        <!--分页的组件-->\r\n      </div>\r\n    </div>\r\n  </div>\r\n</template>\r\n\r\n<script type=\"text/ecmascript-6\">\r\nimport { getPro, GET_City } from '@/utils'   //获取省市\r\nexport default {\r\n  data () {\r\n    return {\r\n      list: [],\r\n      token: window.sessionStorage.getItem('bbytoken'),\r\n      //分页数据\r\n      page: 1, //显示的页码数\r\n      pageCount: 1,\r\n      curId: 0,\r\n      thCurId: 0,\r\n      seCurId: 0,\r\n      threeAuthList: [],\r\n      authList: [],\r\n      regionDetailsVisible: false, //地区详情弹框状态\r\n      detailsPage: 1,    //复核详情当前页数\r\n      detailsCount: 1,  //复核详情总页数\r\n      detailsList: [],  //复核详情列表\r\n      currentReviewData: {}, //当前复核详情\r\n      phone: '', // 负责人联系电话\r\n      throughLoading: [],\r\n      incomeVisible: false, //提现明细弹框状态\r\n      incomePage: 1, //收入明细当前页\r\n      incomeCount: 1, //收入明细总页数\r\n      incomeList: [], //收入明细列表,\r\n      currentIncomeData: {}, //当前收入明细详情\r\n      provinceList: [],//所有省列表\r\n      provinceId: '',//当前所选中的省id\r\n      cityList: [], //市列表\r\n      cityId: '',//当前选中的市id\r\n      company: \"\", //公司名称\r\n      regionList: [], //地区列表\r\n      month: \"\", //月份\r\n    }\r\n  },\r\n\r\n  computed: {\r\n    dateTime () {\r\n\r\n      const dateTime = new Date(this.month)\r\n      const year = dateTime.getFullYear()\r\n      const month = dateTime.getMonth() + 1\r\n      return this.month ? `${year}-${month}` : ''\r\n    }\r\n  },\r\n  watch: {\r\n    company (val) {\r\n      this.page = 1\r\n      this.init()\r\n    }\r\n  },\r\n  methods: {\r\n    paging (e) {\r\n      this.page = e;\r\n      this.init()\r\n    },\r\n    init () {\r\n      this.$axios.post('admin/AgentIncome/index', { token: this.token, page: this.page, city: this.cityId, month: this.dateTime })\r\n        .then(res => {\r\n          this.list = res.data.data.list;\r\n          this.pageCount = res.data.data.rows || 1;\r\n        }).catch(err => { })\r\n    },\r\n    search (time) {\r\n      this.page = 1;\r\n      this.init()\r\n    },\r\n    //地区详情\r\n    async regionDetails (item) {\r\n      try {\r\n        this.regionDetailsVisible = true\r\n        const res = await this.$axios.post('admin/AgentList/regionList', { token: this.token, aid: item.aid })\r\n        this.regionList = res.data.data || []\r\n\r\n      } catch (error) {\r\n        throw (error)\r\n      }\r\n    },\r\n    // 收入明细\r\n    async incomeDetail (item) {\r\n      try {\r\n        this.incomeVisible = true\r\n        const res = await this.$axios.post('admin/AgentIncome/filterDetail', { token: this.token, page: this.incomePage, month: this.dateTime, city_id: item.city_id })\r\n        console.log(res.data.data.list)\r\n        this.incomeList = res.data.data.list || []\r\n        this.incomeCount = res.data.data.rows || 1\r\n      } catch (error) {\r\n        throw (error)\r\n      }\r\n    },\r\n\r\n\r\n    // 获取省\r\n    async getProList () {\r\n      try {\r\n        const data = await getPro()\r\n        this.provinceList = data || []\r\n      } catch (error) {\r\n        throw (error)\r\n      }\r\n    },\r\n\r\n    //获取市\r\n    async getCity () {\r\n      try {\r\n        const data = await GET_City(this.provinceId)\r\n        this.cityList = data || []\r\n        this.cityId = this.provinceId && data[0].id\r\n        this.init()\r\n      } catch (error) {\r\n        throw (error)\r\n      }\r\n    },\r\n    //导出\r\n    EXPort () {\r\n      window.location.href = `${this.baseURL}admin/Login/agentFilter?city=${this.cityId}&month=${this.dateTime}`\r\n    },\r\n    Auth () {  //权限列表\r\n      var id = this.$route.query.id;\r\n      this.curId = id;\r\n      this.$axios.post('admin/Auth/erAuth', {\r\n        token: window.sessionStorage.getItem('bbytoken'),\r\n        id: id\r\n      })\r\n        .then(res => {\r\n          if (res.data.code == 1) {\r\n            var arr = res.data.data;\r\n            for (var i = 0; i < arr.length; i++) {\r\n              if (arr[i].son) {\r\n                if (arr[i].name == '滤芯收入') {\r\n                  this.seCurId = arr[i].id;\r\n                  this.threeAuthList = arr[i].son;\r\n                }\r\n              }\r\n            }\r\n            this.authList = arr;\r\n          } else {\r\n            this.$alert(res.data.msg, '提示', { type: 'error' });\r\n          }\r\n        })\r\n        .catch(err => {\r\n          alert(err);\r\n        })\r\n    }\r\n  },\r\n  mounted () {\r\n    this.Auth()\r\n    this.init()\r\n    this.getProList()\r\n  },\r\n}\r\n</script>\r\n<style scoped>\r\n.region {\r\n  max-height: 400px;\r\n  overflow: auto;\r\n}\r\n.province {\r\n  padding: 20px 0;\r\n}\r\n.city {\r\n  padding: 5px 10px;\r\n  border-top: 1px solid #cccccc;\r\n}\r\n.area {\r\n  padding: 1px 15px;\r\n}\r\n.export {\r\n  float: right;\r\n\r\n  margin-right: 70px;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);

